import { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import ApiFunctions from "../ApiFunctions";
import headerOne from "../imgs/header-bg.jpg"
import Header from "./Header";
import moment from "moment";


const Home = () => {


    const [jobList,setJobList] = useState([]);
    // const [orgList,setOrgList] = useState([]);
    moment.locale('en');
    // let date = "";
    useEffect(() => {
		// ApiFunctions.getAllOrg().then(res => {
        //   var data = res.data.data

        //   setOrgList( data)
        // })


        ApiFunctions.getAllJobs().then(res => {
            var data = res.data.data;

             
              const list = data.map((d,i)=>{
              var date = d.createdDate;
              data[i].date = moment(date).startOf('seconds').fromNow()
              return  data[i];
              });

            setJobList(list)
          })

	}, []);
   
    return (
        <div className="App">
            <Header />
            <div className="home-header head-img" style={{ backgroundImage: `url(${headerOne})` }} >
                <div className="container">
                    <div className="banner-wording">
                        <h1>10000 + jobs added every week</h1>
                        <h3>More than a hundred Fortune 500 companies trust us to find the best fits for them With us, your dream job is just around the corner!</h3>
                        <p>Global job marketing is changing at a swift pace and there are more opportunities for everyone than ever before. We are committed to helping you in finding your dream job that suits your passion.</p>
                        <div className="apply-btn">
                            <Link to="/browse-jobs" className="apply">Apply Now</Link>
                        </div>
                    </div>
                </div>
            </div>

            <div className="pd-main">
                <div className="container">
                    <div className="title-section">
                        <h2>Most Popular Jobs</h2>
                        <p>Let’s have a look at some of the most trending job opportunities listed with us</p>
                    </div>
                    <div className="row">

                    {jobList.map((data, id) => {
                        
                      return  <div className="job-home-wrapper col-lg-6"  key={id}>
                            <div className="inner-box">
                                <div className="content">
                                    <span className="company-logo">
                                        {/* <img src='../imgs/software.png' alt=""  /> */}
                                        <img src={require('../imgs/software.png').default} alt=""  />
                                        </span>
                                    <h4><Link to={"/job-details/"+data._id}>{data.job_title}</Link></h4>
                                    <ul className="job-details">
                                        {/* <li><i className="las la-briefcase"></i> Segment</li>
                                        <li><i className="las la-map-marker"></i> 1933 Perry Street</li> */}
                                        <li><i className="lar la-clock"></i> {data.date}</li>
                                        <li><i className="las la-money-bill"></i> {data.salary} </li>
                                    </ul>
                                    <ul className="job-detail-other">
                                        <li className="time">Full Time</li>
                                        <li className="privacy">Private</li>
                                        <li className="required">Urgent</li>
                                    </ul>
                                </div>
                            </div>
                        </div>

})}
                       
                    </div>
                </div>
            </div>

            <div className="pd-main light-bg">
                <div className="container">
                    <div className="title-section">
                        <h2>Top companies hiring at present </h2>
                        <p>The topmost companies are looking to hire their future assets, do you have what it takes to outshine others? Let’s have a look at the biggest companies with dream job openings for passionate individuals.</p>
                    </div>
                    <div className="row">
                        <div className="col-md-12 col-lg-6">
                            <div className="company-wrapper-content company-wrapper">
                                <div className="company-wrapper-main">
                                    <div className="row">
                                        <div className="col-lg-8 col-md-8 col-sm-8 col-xs-12">
                                            <div className="company-img">
                                                <img src={require('../imgs/company-logo.jpg').default} alt="post_img" />
                                            </div>
                                            <div className="company-right-content company-job-cont">
                                                <h4><Link to="/company-details">ZEN Web Solutions</Link></h4>
                                                <p>Web development jobs </p>
                                                <ul>
                                                    <li><i className="las la-map-marker-alt"></i> 4044 McKinley Avenue</li>
                                                </ul>
                                            </div>
                                        </div>
                                        <div className="col-lg-4 col-md-4 col-sm-4 col-xs-12">
                                            <div className="company-right-btn-content">
                                                <ul>
                                                    <li><Link to="/">7 ACTIVE JOBS</Link></li>
                                                </ul>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="company-keyqord-main">
                                    <ul>
                                        <li><i className="fa fa-tags"></i>Keywords :</li>
                                        <li><Link to="/">ui designer,</Link></li>
                                        <li><Link to="/">developer,</Link></li>
                                        <li><Link to="/">senior</Link></li>
                                        <li><Link to="/">it company,</Link></li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-12 col-lg-6">
                            <div className="company-wrapper-content company-wrapper">
                                <div className="company-wrapper-main">
                                    <div className="row">
                                        <div className="col-lg-8 col-md-8 col-sm-8 col-xs-12">
                                            <div className="company-img">
                                                <img src={require('../imgs/company-logo.jpg').default} alt="post_img" />
                                            </div>
                                            <div className="company-right-content company-job-cont">
                                                <h4><Link to="/company-details">PRODIGY Finance</Link></h4>
                                                <p>Personal finance consultant jobs</p>
                                                <ul>
                                                    <li><i className="las la-map-marker-alt"></i> 15 Downing Street</li>
                                                </ul>
                                            </div>
                                        </div>
                                        <div className="col-lg-4 col-md-4 col-sm-4 col-xs-12">
                                            <div className="company-right-btn-content">
                                                <ul>
                                                    <li><Link to="/">15 ACTIVE JOBS</Link></li>
                                                </ul>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="company-keyqord-main">
                                    <ul>
                                        <li><i className="fa fa-tags"></i>Keywords :</li>
                                        <li><Link to="/">finance,</Link></li>
                                        <li><Link to="/">personal finance consultant,</Link></li>
                                        <li><Link to="/"> risk management</Link></li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-12 col-lg-6">
                            <div className="company-wrapper-content company-wrapper">
                                <div className="company-wrapper-main">
                                    <div className="row">
                                        <div className="col-lg-8 col-md-8 col-sm-8 col-xs-12">
                                            <div className="company-img">
                                                <img src={require('../imgs/company-logo.jpg').default} alt="post_img" />
                                            </div>
                                            <div className="company-right-content company-job-cont">
                                                <h4><Link to="/company-details">NTC Bank</Link></h4>
                                                <p>Assistant Manager Jobs</p>
                                                <ul>
                                                    <li><i className="las la-map-marker-alt"></i> 12-A Campus Street</li>
                                                </ul>
                                            </div>
                                        </div>
                                        <div className="col-lg-4 col-md-4 col-sm-4 col-xs-12">
                                            <div className="company-right-btn-content">
                                                <ul>
                                                    <li><Link to="/">9 ACTIVE JOBS</Link></li>
                                                </ul>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="company-keyqord-main">
                                    <ul>
                                        <li><i className="fa fa-tags"></i>Keywords :</li>
                                        <li><Link to="/">banking,</Link></li>
                                        <li><Link to="/">finance,</Link></li>
                                        <li><Link to="/">assistant manager</Link></li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                        <div className="col-md-12 col-lg-6">
                            <div className="company-wrapper-content company-wrapper">
                                <div className="company-wrapper-main">
                                    <div className="row">
                                        <div className="col-lg-8 col-md-8 col-sm-8 col-xs-12">
                                            <div className="company-img">
                                                <img src={require('../imgs/company-logo.jpg').default} alt="post_img"/>
                                            </div>
                                            <div className="company-right-content company-job-cont">
                                                <h4><Link to="/company-details">XCEED Solutions</Link></h4>
                                                <p>Graphic design, content writing jobs</p>
                                                <ul>
                                                    <li><i className="las la-map-marker-alt"></i> 153, Watson Street</li>
                                                </ul>
                                            </div>
                                        </div>
                                        <div className="col-lg-4 col-md-4 col-sm-4 col-xs-12">
                                            <div className="company-right-btn-content">
                                                <ul>
                                                    <li><Link to="/">15 ACTIVE JOBS</Link></li>
                                                </ul>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div className="company-keyqord-main">
                                    <ul>
                                        <li><i className="fa fa-tags"></i>Keywords :</li>
                                        <li><Link to="/">digital marketing,</Link></li>
                                        <li><Link to="/">graphic design,</Link></li>
                                        <li><Link to="/">senior</Link></li>
                                        <li><Link to="/">it company,</Link></li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div className="pd-main">
                <div className="container">
                    <div className="row">
                        <div className="col-md-12 col-lg-4">
                            <img src={require('../imgs/job-bg.jpg').default} className="img-fluid" alt="" />
                        </div>
                        <div className="col-md-12 col-lg-8">
                            <div className="title-section">
                                <h2>Welcome to Hyre You!</h2>
                                <p>We strongly believe in the idea of coexistence and we look forward to helping as many people as we can to give a head start to their careers. In this competitive world, finding a job can be a tedious task but with us, you can find your ideal job within a few clicks by uploading your resume and directly getting in contact with recruiters from some of the top companies at the helm of globalization. Our mission is to create a prosperous world where people have easy access to a wide spectrum of career opportunities. The internet has immense power in contemporary times and we are reaping this power to bridge the gap between companies looking to hire talented individuals and passionate people striving for their dream jobs. Thus far, we have been able to help more than 20000 individuals to get their offer letters from some of the best companies. Besides, we have more than 300 global MNCs and regional businesses on our employer network who are happy to find the most accomplished candidates through us. We started this endeavor with the view of driving positive changes and we have been successful in doing so.</p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div className="container-fluid">
                <div className="row">
                    <div className="col-lg-6 job-box head-img-two" >
                        <div className="job-inner">
                            <h2>Browse Companies</h2>
                            <p>In this section, you can view the list of companies associated with us and get an elaborative insight into the number of employees they have been able to find through us. Also, you can view their current job openings with all the essential details like salary, perks, and job descriptions. </p>
                            <div className="apply-btn">
                                <Link to="/browse-jobs" className="apply">Browse Companies</Link>
                            </div>
                        </div>
                    </div>
                    <div className="col-lg-6 job-box head-img-three"  >
                        <div className="job-inner">
                            <h2>Browse Jobs</h2>
                            <p>Here, you can browse thousands of jobs that have been categorized based on the job descriptions for your convenience. You can view the jobs and apply for them directly in a few simple steps. If you want us to refine your resume before applying, we can even do that for you.</p>
                            <div className="apply-btn">
                                <Link to="/browse-jobs" className="apply">Browse Job</Link>
                            </div>
                        </div>
                    </div>
                </div>
            </div>


            <div className="pd-main">
                <div className="container">
                    <div className="title-section">
                        <h2>Candidates Testimonials</h2>
                    </div>

                    <div className="testimonial-box">
                        <div className="testimonial-avatar">
                            <img src={require('../imgs/review.jpg').default} alt="" />
                            <img src={require('../imgs/review.jpg').default} alt="" />
                            <img src={require('../imgs/review.jpg').default} alt="" />
                        </div>
                        <div className="testimonial-name">
                            <h4>Darren Sammy</h4>
                            <p>Web Developer</p>
                        </div>
                        <div className="testimonial-word">
                            <p>When all job options seemed to be closing in the horrific times of the COVID-19 pandemic, I found an ideal work-from-home opportunity in web development through this amazing website. Can’t thank them enough for being of great help in finding a job amid the pandemic so that I could support my career and family needs.  I strongly recommend this job listing site to any other site. Also, finding and applying for jobs on this website is super easy and effective.</p>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default Home;